import React from 'react';
import Link, { navigateTo } from 'gatsby-link';
import YellowButtonMD from "./Buttons/YellowButtonMD";
import './News.css';

const News = (props) => {
    return(
        <div className="col-md-3 News-Card" key={props.id} onClick={() => navigateTo(props.path)}>
            <small className="News-Date">{props.date}</small>
            <h2 className="News-Title"><Link to={props.path}>{props.desc}</Link></h2>
            <h3 className="News-Desc">{props.title}</h3>
            <p>by: {props.author}</p>
            <p className="News-Content">{props.children}</p>
            <p className="News-Button">
                <YellowButtonMD path={props.path} >View Details &raquo;</YellowButtonMD>
            </p>
        </div>
    );
};

export default News;


